.logout {
  width: 450px;
  margin: 150px auto 0;
  p {
    font-size: 40px;
    text-align: center;
    line-height: 1.5em;
  }
  .logout-deco {
    width: 220px;
    height: 36px;
    background-image: url('../../image/Decoration.svg');
    background-position: center;
    background-repeat: no-repeat;
    margin: 26px auto;
  }
  .logout-button {
    width: 150px;
    height: 50px;
    text-decoration: none;
    color: $dark-grey;
    border: 1px solid $dark-grey;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
  }
}
